}

.slider-wrapper {
  margin-top: 200px;
}

.item img {
  border: 2px solid #53788b;
  padding: 10px;
  box-sizing: border-box;
  border-radius: 50%;
  transition: 0.5s;
}

.slider__img:hover {
  border: 2px solid transparent;
  box-sizing: border-box;
}

.blur {
  box-sizing: border-box;
  filter: blur(2px);
  transition: 0.5s;
}

.help {
  position: relative;
  width: 560px;
  padding: 0 90px;
}

.slider-img {
  position: relative;
  transition: 0.5s;
}

.slider-img:before {
  content: url(../img/border-line-top.svg);
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-41%, -47%);
  visibility: hidden;
  opacity: 0;
  transition: 0.5s;
}

.slider-img:after {
  content: url(../img/border-line-bottom.svg);
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-54%, 45%);
  visibility: hidden;
  transition: 0.5s;
  opacity: 0;
}

.slider-img:hover:before {
  content: url(../img/border-line-top.svg);
  visibility: visible;
  opacity: 1;
  transition: 0.5s;
}

.slider-img:hover:after {
  content: url(../img/border-line-bottom.svg);
  visibility: visible;
  transition: 0.5s;
  opacity: 1;
}

.slider__img {
  cursor: pointer;
}

.slider {
  max-width: 460px;
  margin: 0 auto;
}

.slider .owl-stage-outer {
  overflow: visible;
}

.transiton {
  transition: 0.5s;
}

.slider__title {
  padding-top: 45px;
  text-align: center;
  color: #396f8c;
  font-size: 20px;
  font-weight: 500;
  text-transform: uppercase;
  letter-spacing: 0.8px;
  transition: 0.5s;
}

.orange {
  color: orange;
}

/* footer */
footer {
  height: 10vh;
}

.footer-block {
  justify-content: space-between;
  opacity: 0.8;
  color: #3e7390;
  font-family: "Gotham Pro";
  font-size: 14px;
  font-weight: 400;
  padding: 30px 0;
}

.footer-block span:hover {
  opacity: 0.5;
  cursor: pointer;
}

.address {
  display: flex;
}

.address__item {
  margin-right: 40px;
  position: relative;
  display: flex;
  align-items: center;
}

.address__item:last-child {
  margin-right: 0;
}
